.onglet-container{
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 8px 0;
}

.onglet-container a{
    text-decoration: none;
    color: #ededed;
    font-size: 18px;
    padding: 15px 5px 17px 5px;
    border-bottom: solid 3px transparent;
    font-weight: bold;
    transition: all .3s ease-in-out;
}

.onglet-container .activeLink{
    /* border-bottom: solid 3px #00BBFF; */
    border-bottom: solid 3px #14dc1b;
}