.profil-image-container{
    width: 100%;
    height: auto;
    position: relative;
    padding-bottom: 5px;
}
p{
    margin: 0 15px;
}

/* =========== Banniere Picture ================== */
.profil-image-container .banniere-img{
    height: 120px;
    background-image: url('../../assets/banniere.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}

/* =========== Profil Name ================== */
.profil-name{
    display: flex;
    align-items: center;
    height: auto;
    position: sticky;
    top: 0;
    margin: 40px 0 0 0;
    padding: 10px 0 10px 15px;
    font-size: 23px;
    font-weight: 900;
    backdrop-filter: blur(10px);
    z-index: 1110;
}
.profil-name img{
    margin: 0 5px;
}

/* =========== Profil Bio ================== */
.profil-bio{
    font-size: 18px;
    margin: 10px 0 15px 0;
}
.profil-bio p img{
    margin: 0 5px;
}
.profil-bio p{
    display: flex;
    align-items: center;
}