.contact-button{
    outline: none;
    width: 140px;
    text-align: center;
    cursor: pointer;

    border: solid 2px #fff;
    font-size: 17px;
    font-weight: bold;
    padding: 5px 10px;

    background-color: #000;
    color: #fff;
    border-radius: 9999px;

    position: absolute;
    top: 128px;
    right: 10px;
}

.button-open{
    border-color: crimson;
    color: crimson;
}