.post-container {
  position: relative;
    display: flex;
    align-items: flex-start;
    padding: 16px;
    border: 1px solid #555;
    border-radius: 8px;
    margin: 15px 15px 0 15px;
  }
  
  .avatar {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 10px;
    flex-shrink: 0;
    background-image: url('../../assets/pp.png');
    background-size: cover;
  }
  
  .post-content {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .post-header {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  
  .post-name {
    margin-right: 8px;
    font-weight: bold;
    font-size: 19px;
  }
  
  .post-username {
    color: #777;
    margin-right: 8px;
  }
  
  .post-date {
    color: #777;
  }
  
  .post-body {
    margin-top: 8px;
    margin-bottom: 16px;
  }
  .post-body a{
    color: #14dc1b;
    overflow-wrap: anywhere;
    font-weight: bold;
  }

  .post-image{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
  }

  .post-image img{
    width: calc(50% - 8px);
    /* min-width: 125px; */
    height: 20vw;
    max-height: 130px;
    margin: 4px;
    border-radius: 5px;
    object-fit: cover;
  }

  .post-image.single-image img {
    width: 100%;
    min-height: 250px;
    margin: 0;
  }


  /* ======== MEDIA QUERY ==========*/
  @media screen and (max-width: 360px) {
    .post-image img{
      width: calc(100% - 8px);
      height: 100px;
    }
  }
