.image-modal {
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100vw;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1111;
}

.image-modal-content {
    max-width: 95vw;
    max-height: 95vh;
    background-color: #f1f1f1;
    text-align: center;
    padding: 5px;
    position: relative;
    overflow: auto;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-modal-content img{
    width: 100%;
    max-height: 95vh;
    border-radius: 10px;
}

.image-modal-close {
    position: absolute;
    top: 2px;
    right: 2px;
    background-color: #f1f1f1;
    border-bottom-left-radius: 5px;
    border-top-right-radius: 5px;
    cursor: pointer;
    padding: 5px;
    width: 20px;
    height: 20px;
    background-image: url('../../../assets/close.svg');
    background-size: cover;
    background-repeat: no-repeat;
}