.form-contact {
    z-index: 1112;
    position: absolute;
    top: 200%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(90% - 40px);
    border: solid 3px #fff;
    border-radius: 20px;
    background-color: #000;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 20px;
}

.form-contact p {
    text-align: center;
    line-height: 1;
}

.form-contact p a {
    color: #14dc1b;
    font-weight: bold;
}

.form-contact .form-contact-close {
    position: relative;
    right: -8px;
    top: -8px;
    width: 30px;
    height: 30px;
    font-weight: bold;
    font-size: 20px;
    background-color: #000;
    color: red;
    border: solid 2px red;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: end;
    line-height: 0;
}

.form-contact input {
    outline: none;
    border: none;
    border-bottom: solid 2px #fff;
    margin: 20px 0 0;
    width: calc(100% - 10px);
    background-color: #000;
    color: #fff;
    font-size: 16px;
    padding: 5px;
    transition: all .2s ease-in-out;
}


.form-contact textarea {
    margin: 20px 0 0;
    width: calc(100% - 10px);
    height: 80px;
    resize: none;
    border: solid 1px #fff;
    background-color: #000;
    color: #fff;
    font-size: 16px;
    outline: none;
    padding: 5px;
    border-radius: 5px;
}



/*====================  BUTTON SEND ==================*/
.form-contact button {
    position: relative;
    margin-top: 20px;
    padding: 10px 20px;
    border-radius: 7px;
    border: 1px solid #14dc1b;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    cursor: pointer;
    background: transparent;
    color: #fff;
    overflow: hidden;
    box-shadow: 0 0 0 0 transparent;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.form-contact button:hover {
    background: #14dc1b;
    box-shadow: 0 0 30px 5px rgba(20, 220, 27, 0.815);
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
}

.form-contact button:hover::before {
    -webkit-animation: sh02 0.5s 0s linear;
    -moz-animation: sh02 0.5s 0s linear;
    animation: sh02 0.5s 0s linear;
}

.form-contact button::before {
    content: '';
    display: block;
    width: 0px;
    height: 86%;
    position: absolute;
    top: 7%;
    left: 0%;
    opacity: 0;
    background: #fff;
    box-shadow: 0 0 50px 30px #fff;
    -webkit-transform: skewX(-20deg);
    -moz-transform: skewX(-20deg);
    -ms-transform: skewX(-20deg);
    -o-transform: skewX(-20deg);
    transform: skewX(-20deg);
}

@keyframes sh02 {
    from {
        opacity: 0;
        left: 0%;
    }

    50% {
        opacity: 1;
    }

    to {
        opacity: 0;
        left: 100%;
    }
}

.form-contact button:active {
    box-shadow: 0 0 0 0 transparent;
    -webkit-transition: box-shadow 0.2s ease-in;
    -moz-transition: box-shadow 0.2s ease-in;
    transition: box-shadow 0.2s ease-in;
}