/* =========== Profil Picture ================== */
.profil-image-container .profil-picture{
    position: absolute;
    left: 15px;
    top: calc(120px - 60px);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: start;
    background-color: #000;
    overflow: hidden;
    border: solid 5px #000;
}
.profil-image-container .profil-picture img{
    width: 110px;
    height: 110px;
    border-radius: 50%;
}