.arrowUp{
    width: 40px;
    height: 40px;
    background-image: url('./arrow-up.svg');
    background-size: cover;
    background-repeat: no-repeat;

    position: fixed;
    z-index: 1000;
    bottom: 20px;
    right: 20px;

    background-color: #00BBFF;
    border-radius: 50%;
    padding: 5px;
    cursor: pointer;

    opacity: 0;
    transition: opacity .3s ease-in-out;
    
}

.arrowUp.arrow-visible{
    opacity: 1;
}