.contact-modal {
    position: absolute;
    top: 170px;
    right: 100px;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1111;
}

.contact-modal-content ul{
    list-style-type: none;
    width: fit-content;
    height: auto;
    background-color: #000;
    border: solid 3px #fff;
    border-radius: 15px;
}

.contact-modal-content ul li{
    border-top: solid 2px #fff;
}

.contact-modal-content ul li:nth-child(1){
    border: none;
}

.contact-modal-content ul li .contact-modal-item{
    padding: 10px 10px 10px 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    text-decoration: none;
    transition: background-color .3s ease-in-out;
}

.contact-modal-content ul li .contact-modal-item:hover{
    background-color: #14dc1b;
}

.contact-modal-content ul li:nth-child(1) .contact-modal-item{
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.contact-modal-content ul li:last-child .contact-modal-item{
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}


.contact-modal-content ul li .contact-modal-item img{
    margin: 0 5px;
}
